* {
  margin: 0;
  border: 0;
  padding: 0;
  
}

body {
  height: 90vh;
  display: flex;
  justify-content: center;
  background-color: #4ba9b9;
}

.App button {
  display: inline-block;
  text-align: center;
  font-size: 100px;
  margin-top: 50%;
  background-color: #08686e;
  color: white;
}

.App button:hover {
  cursor: pointer;
  background-color: #8395a7;
}